  --portfolioCardTranslucent: rgba(155, 167, 192, .75);\n    --descriptionCardTranslucent: rgba(233, 236, 239, .75);\n    --imageBackgroundTranslucent: rgba(206, 212, 218, .75);\n\n}\n\n\nbody {\n    width: 100%;\n    font-size: 1vw;\n    text-wrap: balance;\n    font-family: Verdana, Geneva, Tahoma, sans-serif;\n    margin: 0;\n    min-height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    background-color: var(--darkness);\n    a {\n        cursor: pointer;\n        text-decoration: none;\n    }\n\n    img {\n        width: 100%;\n        cursor: pointer;\n    }\n}\n\n.Portfolio {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n    background: linear-gradient(rgb(46, 40, 42))\n}\n\n.bio {\n    display: flex;\n    flex-direction: column;\n    width: 80%;\n    margin-top: .5rem;\n    font-size: 1.5rem;\n    text-align: center;\n    align-items: center;\n    justify-content: center;\n}\n\n.headerText {\n    font-family: 'Courier New', 'Courier', 'monospace';\n    font-weight: bold;\n    font-size: 2rem;\n    margin-bottom: .5rem;\n    font-style: italic;\n\n}\n\n.ProDetails {\n    border-radius: 2vmin;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    margin: 1rem;\n    padding: 3rem;\n    background: rgba(248, 249, 250, .75);\n    border: solid 1px var(--darkness);\n    box-shadow: 2px 2px 2px var(--darknessTranslucent);\n    width: 50vmin;\n\n    opacity: 0;\n    transform: translateX(-5vmin);\n    animation: fadeInFromRight 2s ease forwards;\n}\n\n.portfolioSamples {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    margin: .5rem;\n    padding: .5rem;\n    opacity: 0;\n    transform: translateY(5vmin);\n    animation: fadeInFromBottom 5s ease forwards;\n}\n\n.AboutMe {\n    border: solid 1px rgba(250, 216, 214, .5);\n    display: flex;\n    flex-direction: column;\n    margin: 1rem;\n    padding: 2rem;\n    background: linear-gradient(white, rgba(171, 127, 242, 0.2));\n    font-size: 1.25rem;\n    width: 70%;\n    align-items: center;\n    justify-content: center;\n    border-radius: 2vmin;\n    opacity: 0;\n    transform: translateY(-2.5vmin);\n    animation: fadeInFromTop 2s ease forwards;\n\n}\n\n.ProPicContainer {\n    display: flex;\n    margin: 1rem;\n}\n\n.ProPic {\n    width: 30vmin;\n    border-radius: 50%;\n    opacity: 0;\n    animation: fadeInStatic 4s ease forwards;\n}\n\n.displayCard {\n    display: flex;\n    flex-direction: column;\n}\n\n.portDescriptionCard {\n    width: 80%;\n    border-radius: 2vmin;\n    border: solid 2px var(--light);\n    background-color: rgba(46, 40, 42, .3);\n    margin: .5rem;\n    padding: .5rem;\n    font-size: 1rem;\n    box-shadow: 1px 3px 3px rgb(46, 40, 42);\n    opacity: 0;\n    transform: translateX(-5vmin);\n    animation: fadeInFromLeft 8s ease forwards;\n\n}\n\n\n.portfolioCard {\n    display: flex;\n    flex-direction: column;\n    border-radius: 2vmin;\n    background: linear-gradient(rgba(171, 127, 242, 0.5), white);\n    border: solid 2px var(--light);\n    margin: 1rem;\n    padding: 1rem;\n    width: 60%;\n    box-shadow: 1px 3px 3px rgb(250, 216, 214);\n}\n\n/* .portfolioCard:hover {\n    background: linear-gradient(rgba(171, 127, 242, 0.5), white);\n} */\n\n.portPiece {\n    justify-content: center;\n    align-items: center;\n    display: flex;\n}\n\n@media only screen and (max-width: 900px) {\n    .portPiece {\n        flex-direction: column;\n    }\n}\n\n.portPicContainer {\n    z-index: 1;\n}\n\n.picFile {\n    width: 35vmin;\n    height: 20vmin;\n    border: 5vmin;\n    margin: 1rem;\n    border: solid 2px var(--darkness);\n    background-color: var(--light);\n    padding: 1rem;\n    border-radius: 10px 200px / 100px;\n    box-shadow: 2px 2px 2px var(--darknessTranslucent);\n    z-index: 2;\n    opacity: 0;\n    animation: fadeInStatic 4s ease forwards;\n}\n\n.certPicFile {\n    width: 35vmin;\n    border: 5vmin;\n    margin: 1rem;\n    border: solid 2px var(--darkness);\n    padding: 1rem;\n    box-shadow: 2px 2px 2px var(--darknessTranslucent);\n    z-index: 2;\n    opacity: 0;\n    animation: fadeInStatic 4s ease forwards;\n}\n\n.picFile:hover {\n    box-shadow: 3px 5px 5px var(--darknessTranslucent);\n    scale: 125%;\n    transition: 1s;\n}\n\n.ContactsBar {\n    width: 80%;\n    height: -moz-fit-content;\n    height: fit-content;\n    border-radius: 2vmin;\n    display: flex;\n    margin: 1rem;\n    padding-top: .5rem;\n    background-color: var(--darknessTranslucent);\n    border: solid 3px black;\n    justify-content: space-around;\n    align-items: center;\n}\n\n@media only screen and (max-width: 900px) {\n    .buttonLinks {\n        flex-direction: column;\n    }\n}\n\n\n\n\n.contactIcon {\n    width: 8vmin;\n    padding: .25rem;\n}\n\n.contactPic:hover {\n    box-shadow: 4px 8px 8px var(--darknessTranslucent);\n    scale: 125%;\n    transition: 1s;\n}\n\n.contactPic {\n    box-shadow: 1px 2px 2px var(--darknessTranslucent);\n    width: 8vmin;\n    height: 8vmin;\n    border-radius: 50%;\n}\n\n@media only screen and (max-width: 900px) {\n    .contactPic {\n        width: 15vmin;\n        height: 15vmin;\n    }\n}\n\nbutton.resume {\n    text-align: center;\n    width: 20rem;\n    box-shadow: 1px 2px 2px var(--darknessTranslucent);\n    padding: 1rem;\n    margin: .5rem;\n    font-weight: bold;\n    font-size: 2rem;\n    border-radius: 2vmin;\n}\n\nbutton:hover {\n    box-shadow: 4px 8px 8px var(--darknessTranslucent);\n    cursor: pointer;\n    scale: 150%;\n    transition: 1s;\n    background-color: white;\n}\n\n.buttonLinks {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 90%;\n    max-width: 400px;\n    margin: .5rem;\n    opacity: 0;\n    animation: fadeInStatic 6s ease forwards;\n}\n\n.portTitleCard {\n    justify-content: center;\n    align-items: center;\n    background-color: white;\n    border: solid 2px var(--royalPurple);\n    padding: 1rem;\n    margin: 1rem;\n    border-radius: 2vmin;\n    box-shadow: 2px 2px 2px var(--darknessTranslucent);\n    border: solid 2px var(--darkness);\n}\n\n.portTitle {\n\n    justify-content: center;\n    align-items: center;\n    font-size: 3vmin;\n    text-align: center;\n    color: var(--darkness);\n\n/*    overflow: break-word; */\n}\n\n.portTitle:hover {\n    color: var(--royalPurple);\n    cursor: pointer;\n}\n\n.descriptionList {\n    padding: 2rem;\n    justify-content: center;\n    align-items: center;\n    overflow-wrap:break-word;\n    background-color: var(--light);\n    text-align: space-around;\n    border-radius: 2vmin;\n    border: solid 3px var(--darkness);\n    box-shadow: 6px 3px 3px var(--darknessTranslucent);\n}\n\n.descriptionList:hover {\n    background-color: white;\n}\n\n.portDescriptionText {\n    padding: 1rem;\n    text-align: right;\n    list-style: none;\n    border-top: solid 2px var(--royalPurple);\n    border-bottom: solid 2px var(--darkness);\n}\n\n.bioBullet {\n    border-radius: 2vmin;\n    padding: 1vmin;\n    margin: .5vmin;\n    width: 70%;\n    font-family: 'Courier New', 'Courier', 'monospace';\n    background: rgba(248, 249, 250, .75);\n    font-weight: bold;\n    text-align: center;\n    justify-content: center;\n    align-items: center;\n}\n\nh1 {\n    color: whitesmoke;\n    text-align: center;\n    font-family: 'Courier New', 'Courier', 'monospace';\n\n}\n\n\n.scrollNav {\n    height: 15%;\n    position: fixed;\n    top: 0;\n    z-index: 3;\n    ul {\n        flex-direction: row;\n    list-style-type: none;\n    }\n    width: 100%;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    background-color: rgba(171, 127, 242, 0.2);\n    box-shadow: 5px 3px 1px black;\n}\n\n.navLinks {\n    width: 90%;\n    margin: 1rem;\n    padding: 1rem;\n    justify-content: space-between;\n    font-size: 5vmin;\n}\n\n.linkButton {\n    height: 90%;\n    width: 90%;\n    padding: .5rem;\n    margin-left: .75rem;\n    color: black;\n    background-color: white;\n    border-radius: 2vmin;\n}\n\n.linkButton:hover {\n    color: white;\n    background-color: rgba(171, 127, 242, .4);\n}\n\n.shadowBox {\n    z-index: 0;\n    visibility: hidden;\n}\n@media only screen and (max-width: 900px) {\n    .shadowBox {\n        margin: 1rem;\n    }\n}\n\n.aboutText {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    font-size: 4vmin;\n    margin: 1vmin;\n    overflow: hidden;\n    white-space: nowrap;\n    letter-spacing: .15em;\n    animation: typing 3.5s steps(40, end), blink-caret, .75s step-end infinite;\n}\n\n.languageButton {\n    border-radius: 5vmin;\n    width: 20vmin;\n    height: 3vmin;\n    -o-object-fit: cover;\n       object-fit: cover;\n    margin: 1rem;\n    padding: 2rem;\n}\n\n.languageContainer {\n    display: inline-block;\n    padding: .5rem;\n\n}\n\n.heading {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    text-align: center;\n    align-items: center;\n    overflow-wrap: break-word;\n    word-wrap: break-word;\n    font-family: 'Courier New', Courier, monospace;\n    color: white;\n    font-weight: bold;\n    text-align: center;\n    width: 90%;\n    margin: 4rem;\n}\n@media only screen and (max-width: 900px) {\n    .heading {\n        margin-top: 5rem;\n    }\n}\n"],"sourceRoot":""}]);
